import React, { useState } from "react";
import ItemExpanded from "./itemExpanded";

export default function Item(props) {
  const [isOpened, setIsOpened] = useState(false);

  const toggle = (e) => {
    setIsOpened((wasOpened) => !wasOpened);
  };

  const { movie, index } = props;

  return isOpened ? (
    <div>
      <ItemExpanded movie={movie} toggle={toggle} />
    </div>
  ) : (
    <div className="movie-item  col-sm-2 col-12" key={index}>
      <div className="card">
        <img
          src={
            "https://image.tmdb.org/t/p/w220_and_h330_face/" + movie.poster_path
          }
        />
        <h5 className=" my-title bold mt-3">{movie.title}</h5>
        <div className="vote">
          <p>
            Rating:
            <span class="badge badge-dark ">{movie.vote_average}</span>
          </p>
        </div>
        <div className="vote">
          <p>Release:</p>
          <span class="badge badge-dark ">{movie.release_date}</span>
        </div>

        <div className="box mb-3">
          <div className="btn btn-warning" onClick={(e) => toggle(e)}>
            <div>Show More</div>
          </div>
        </div>
      </div>
    </div>
  );
}
