import React from "react";

export default function ItemExpanded(props) {
  const { movie } = props;

  return (
    <div class="media">
     
      <img
        src={
          "https://image.tmdb.org/t/p/w220_and_h330_face/" + movie.poster_path
        }
        class="align-self-center mr-3"
        alt="..."
      ></img>   
     
      <div class="media-body">
    <h5 class="mt-0">{movie.title}</h5>
        <p>
        
        </p>
        <p class="mb-0">
        <p>{movie.overview}</p>
        </p>
      </div>
      <button type="button" class="ml-2 mb-1 close" onClick={props.toggle} data-dismiss="toast" aria-label="Close">
      <span aria-hidden="true">&times;</span>
    </button>
    </div>
  );
}
